.container {
  display: flex;
  flex-wrap: wrap;
  padding: 60px;
  background-image: linear-gradient(
    360deg,
    rgb(20 22 67) 0%,
    rgba(15, 15, 47, 1) 15%,
    rgba(2, 1, 19, 1) 100%
  );
}

.leftContainer {
  flex: 60%;
  max-width: 60%;
}

.rightContainer {
  flex: 40%;
  max-width: 40%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.rightContainer > * {
  padding-left: 15px;
}

@media (max-width: 992px) {
  .leftContainer,
  .rightContainer {
    flex: 100%;
    max-width: 100%;
  }
}
