.content {
  padding: 10px;
}

.formAlign {
  flex-direction: row-reverse !important;
  padding: 52px;
}

.formContainer {
  display: grid;
}

.formElement {
  margin: 10px 0px !important;
  color: white;
}

.title, .subtitle {
  margin-bottom: 30px;
}

.title {
  text-transform: uppercase; 
  letter-spacing: 3px;
}

.subtitle {
  font-size: 50px !important;
  font-family: Quarkstorm !important;
  color: white;
  -webkit-text-stroke-color: #9B1085 !important;
  -webkit-text-stroke-width: 1px !important;
}

@media (max-width: 600px) {
  .title {
    font-size: 2rem !important;
  }
}
