.title {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  color: #292d4f;
  line-height: 1.2;
  font-size: 36px;
  margin: 20px 0px;
}

.contactInformation {
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 10px;
}

.contactInformation,
.message {
  display: grid;
}

.formElement {
  margin: 10px 0px !important;
}

.formContainer {
  width: 1140px;
  max-width: 80vw;
}

.card {
  background: #915fb933;
  padding: 25px;
  border-radius: 15px;
}
