.detailsContainer {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
}

.title {
  color: #F8F9D5 !important;
  font-family: CodaExtraBold;
  -webkit-text-stroke-color: #FFFAFA80;
  -webkit-text-stroke-width: 2px;
  font-size: 20px;
}

.titleActive {
  -webkit-text-stroke-color: #C304F280;
}

.featuresContainer {
  padding: 15px;
  overflow-x: hidden;
}

@media (max-width: 900px) {
  .detailsContainer {
    grid-template-columns: 1fr;
  }
}
