.container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 25px 0px;
}

.rightContainer {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.leftContainer {
  max-width: 100vw;
}

.leftContainer > * {
  padding-left: 60px;
}

.mockup {
  max-height: 600px;
  max-width: calc(100% - 30px);
}

.sectionName {
  line-height: 1.2;
  font-size: 50px;
  margin-bottom: 50px;
  padding-left: 15px;
}

@media (max-width: 1200px) {
  .container {
    grid-template-columns: 1fr;
  }

  .leftContainer > * {
    padding-left: 10px;
  }
}
