.statistics {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  padding: 0px 60px;
  grid-column-gap: 10px;
  background-image: linear-gradient(0deg, rgb(27 9 41), rgb(1 1 3));
  padding: 60px;
}

@media (max-width: 900px) {
  .statistics {
    grid-template-columns: 1fr;
    padding: 15px 30px;
  }
}
