.section {
  list-style: none;
  margin: 30px 0px;
  font-size: 20px;
  font-family: Coda;
  color: #50d78e;
}

.section b {
  font-family: CodaExtraBold;
}

.sections {
  padding: 0px;
  background: #00000080;
  padding: 10px;
  border-radius: 20px;
}

.title,
.subtitle {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  color: #ffffff;
  line-height: 1.2;
  font-weight: bold;
  -webkit-text-stroke-width: 2px;
}

.title {
  font-size: 60px;
  margin-bottom: 40px;
}

.subtitle {
  margin-bottom: 0.5rem;
  font-size: 35px;
  font-family: Roboto;
  -webkit-text-stroke-color: #455cb0;
}

.tooltip {
  color: white !important;
  background-color: #140202 !important;
  padding: 15px !important;
  font-size: 15px !important;
  font-family: Joystix !important;
}
